<template>
  <div class="main">
    <div class="row">
      <div class="container">
        <div class="coursePageAll">
          <div class="coursePageTitle">
            <h3>{{ Title }}</h3>
          </div>
          <div class="tags" v-if="$route.params.id != 1">
            <router-link active-class="act" to="/category/all">全部</router-link>
            <router-link
              active-class="act"
              :to="`/category/${c.LessonCategoryID}`"
              v-for="c in LessonCatrgories"
              :key="c.LessonCategoryID"
              v-show="c.LessonCategoryID > 1"
              >{{ c.CategoryName }}</router-link
            >
          </div>
          <Lessons :key="$route.params.id" :loader="loadData" :tag="Title" />
        </div>
        
      </div>
    </div>
  </div>
</template>
<script>
import Lessons from "@/components/Lessons.vue";
export default {
  components: {
    Lessons,
  },
  data() {
    return {
      Title: "學生課程",
      LessonCatrgories: [],
    };
  },
  async mounted() {
    this.LessonCatrgories = await this.$api.getLessonCategories();
    this.load();
  },
  methods: {
    load() {
      if (this.$route.params.id == 1) {
        this.Title = "師培課程";
      } else {
        this.Title = "學生課程";
      }
    },
    loadData() {
      if (this.$route.params.id.toLowerCase() == "all") {
        return this.$api.getStudentLessons();
      }
      return this.$api.getLessonByCategory(this.$route.params.id);
    },
  },
  watch: {
    $route() {
      this.load();
    },
  },
};
</script>